import React from 'react';
import ReactPaginate from 'react-paginate';
import styles from './pagination.module.scss';

export const Pagination = ({ currentPage, onChangePage, pageCount = 3 }) => {
  //  selected (0-based  ReactPaginate)
  const [selected, setSelected] = React.useState(currentPage - 1);

  //   selected,   currentPage ,
  //          .
  React.useEffect(() => {
    const desired = currentPage - 1;
    if (desired !== selected) {
      setSelected(desired);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentPage]);

  const handlePageChange = (event) => {
    const newSelected = event.selected;
    //       
    if (newSelected === selected) return;

    setSelected(newSelected); //     UI  
    const newPageNumber = newSelected + 1;
    onChangePage(newPageNumber);
  };

  return (
    <ReactPaginate
      className={styles.root}
      breakLabel="..."
      nextLabel=">"
      previousLabel="<"
      onPageChange={handlePageChange}
      pageRangeDisplayed={4}
      pageCount={pageCount}
      forcePage={selected} //  forcePage   
      renderOnZeroPageCount={null}
    />
  );
};

export default Pagination;